import React, { useState,useEffect } from "react";
import { useNavigate } from 'react-router-dom';
import styles from './style.module.scss'
import { SearchBar } from "components";
import { numberWithCommas } from "utils/Commas";
import useOrderList from "hooks/useOrderList";
import useProductList from "hooks/useProductList";
import API from "api";
 
function OrderListContent() {
  const navigate = useNavigate();
  const [filter,setFilter] = useState({
    category:{ 
      ALL : { checked : true},
      QR : { checked : true},
      laser : { checked : true},
      print : { checked : true},
      embroidery : { checked : true},
    },
    start:null,
    end:null,
    keyword:""
  },
  );
  const [products] = useProductList();
  const [list, setList,refreshEvent] = useOrderList();
  const [tabIndex,setTabIndex] = useState(0);
  const [optionMap,setOptionMap] = useState({});


  useEffect(()=>{
    setList((l) => { 
      l.map((value)=> { 
        if(filter.category)
          value.isHide = !(filter.category[value.category].checked);
        else
          value.isHide = false;
      })
      return [...l]
    });

    refreshEvent(filter);
  },[filter]);
  
  useEffect(()=>{
    let objectMap = {};
    products.map((value)=>{
      value.options && value.options.map((v)=>{
        objectMap[v.id] = { option : v , product : value};
      })
    })
    setOptionMap(objectMap);    
  },[products])

  const LinkClick = (value,path) => {
    return navigate(`/order/detail/${value.category}/${path}`,{state:value});
  }

  const activeStyle = (type) => {
    const ORDER_STATE ={"QR": {color:"#2266AB"},"print":{color:"#22AB2E"},"embroidery":{color:"#5C22AB"},"laser":{color:"#AB3222"}};
    return ORDER_STATE[type] ? ORDER_STATE[type].color : "#2266AB";
  }
  const detailTitle = (type) => {
    const ORDER_STATE ={"QR": {title:"PHOTO SCAN PRINTING"},"print":{title:"NBY DIGITAL PRINTING"},"laser":{title:"LASER GUN PRINTING"},"embroidery":{title:"EMBROIDERY"}};
    return ORDER_STATE[type] ? ORDER_STATE[type].title : "PHOTO SCAN PRINTING";
  }

  function getListLength(paramList){
    return paramList && paramList.filter(v=>((filter.category[v.category].checked) || filter.category["ALL"].checked)).length;
  }

  const getTotalPrice = (value) => {

    
    let state = value;

    state.price = ((optionMap[value.option_id] && optionMap[value.option_id].product ? optionMap[value.option_id].product.price : 0))
    const categoryPrice = { QR : 15000, print : 15000, embroidery : 3000,laser:3000,laser_pre : 3000};
    let frontCount = state.assets ? state.assets.length : 0;
    let resultPrice = 0;


    if(state.category === "embroidery"){
        resultPrice = (state.price + ((optionMap[value.option_id] && optionMap[value.option_id].option ? optionMap[value.option_id].option.price : 0)) + categoryPrice[state.category]);
    }
    else if(state.category === "laser" || state.category === "laser_pre"){
        resultPrice = state.price + categoryPrice[state.category];
    }
    else{
        resultPrice = (state.price + ((optionMap[value.option_id] && optionMap[value.option_id].option ? optionMap[value.option_id].option.price : 0)) 
        + ((state.work_img ? 1 : 0) * categoryPrice[state.category])
        + ((state.work_img_back ? 1 : 0) * categoryPrice[state.category])
        )
    }

    return resultPrice.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");

  }

  function getModifyDate(value){
    try{
      let date = new Date(value.modified_at.replace("T"," ").split(".")[0]);
      date.setHours(date.getHours() + 7);

      function zeroCheck(num){
        return num < 10 ? "0"+num : num;
      }
      return date.getFullYear().toString().slice(2,4) + ":" + zeroCheck(date.getMonth() + 1) + ":" + zeroCheck(date.getDate());
    }
    catch(e){
      return "";
    }
    
  }

  return (
    <div className={styles.Container}>
      <SearchBar tabIndex={tabIndex} setTabIndex={setTabIndex} filter={filter} setFilter={setFilter}/>
      <div className={styles.OrderListWrap}>
        <div className={styles.OrderInner}>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>작업대기<span>{getListLength(list.filter(v=>v.status === "order"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "order" && ((filter.category[v.category].checked) || filter.category["ALL"].checked)).map((value,index)=>{
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.category)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.category)}</p> <span>{getModifyDate(value)}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(getTotalPrice(value))}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  );
                  
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>작업중<span>{getListLength(list.filter(v=>v.status === "wip"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "wip" && ((filter.category[v.category].checked) || filter.category["ALL"].checked)).map((value,index)=>{
                  

                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.category)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.category)}</p> <span>{getModifyDate(value)}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(getTotalPrice(value))}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>픽업대기<span>{getListLength(list.filter(v=>v.status === "done"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "done" && ((filter.category[v.category].checked) || filter.category["ALL"].checked)).map((value,index)=>{
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.category)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.category)}</p> <span>{getModifyDate(value)}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(getTotalPrice(value))}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>결제완료<span>{getListLength(list.filter(v=>v.status === "paid"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "paid" && ((filter.category[v.category].checked) || filter.category["ALL"].checked)).map((value,index)=>{
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.category)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.category)}</p> <span>{getModifyDate(value)}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(getTotalPrice(value))}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
          <div className={styles.OrderBox}>
            <p className={styles.OrderTitle}>주문취소<span>{getListLength(list.filter(v=>v.status === "cancel"))}개</span></p>
            <div className={styles.ListWrap}>
              {
                list.filter(v=>v.status === "cancel" && ((filter.category[v.category].checked) || filter.category["ALL"].checked)).map((value,index)=>{
                  return(
                    <div className={styles.ListBox}>
                      <div style={{backgroundColor: activeStyle(value.category)}} className={styles.ListTitleBox}>
                        <p>{detailTitle(value.category)}</p> <span>{getModifyDate(value)}</span>
                      </div>
                      <div className={styles.ListContent}>
                        <p>NAME : {value.name}</p>
                        <p>PHONE : {value.phone}</p>
                        <p>총 결제 금액 : <span>{numberWithCommas(getTotalPrice(value))}원</span></p>
                        <button onClick={()=>{LinkClick(value,value.id)}}>자세히 보기</button>
                      </div>
                    </div>
                  )
                })
              }
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OrderListContent;
