import { useState, useEffect } from "react";
import API from 'api';

const useListState = (type) => {
  const [list, setList] = useState([]);

  useEffect(()=>{
    getOrderList();
  },[type]);

  useEffect(() => {
    const executeCallback = () => {
      getOrderList();
    };
    const timerId = setInterval(executeCallback, 3000);
    return () => clearInterval(timerId);
  }, []);

  const getOrderList = async (filter) => {

    let url = `order`;
    console.log(filter)
    for(let key in filter){
      if(filter[key] && key != "category")
        url += (url[url.length - 1] === "&" ? "" : "?") + key + "=" + filter[key] + "&";
    }
    if(url[url.length - 1] === "&")
      url = url.slice(0,-1);
      console.log(url);

    let list = await API.getOrders(url);
    setList([...list && list.order_list ? list.order_list : []]);
  }

  return [list , setList , getOrderList]
};

export default useListState;